.sidebar {
  width: 244px;
  height: 100vh;
  background-color: #000;
  border-right: 1px solid #262626;
  padding: 8px 12px 20px;
  position: fixed;
  left: 0;
  top: 0;
}

.logo h1 {
  font-family: 'Instagram Sans', -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 24px;
  font-weight: 600;
  color: #fff;
  margin: 25px 0 19px 12px;
  cursor: pointer;
}

.nav-menu {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.nav-item {
  display: flex;
  align-items: center;
  padding: 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
  position: relative;
}

.nav-item:hover {
  background-color: #121212;
}

.nav-item.active {
  font-weight: 700;
}

.nav-icon {
  font-size: 24px;
  color: #fff;
  margin-right: 16px;
  width: 24px;
  height: 24px;
}

.nav-item span {
  font-size: 16px;
  color: #fff;
  font-weight: 400;
}

.nav-item.active span {
  font-weight: 700;
}

.nav-icon-container {
  position: relative;
  margin-right: 16px;
}

.notification-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  background-color: #ed4956;
  color: #fff;
  font-size: 11px;
  font-weight: 600;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #000;
}

.notification-dot {
  position: absolute;
  top: -2px;
  right: -2px;
  background-color: #ed4956;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  border: 2px solid #000;
}

.profile-pic {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #262626;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
}

.profile-pic svg {
  color: #fff;
  font-size: 14px;
}

/* Responsive design */
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
    border-right: none;
    border-bottom: 1px solid #262626;
    padding: 8px;
  }
  
  .nav-menu {
    flex-direction: row;
    overflow-x: auto;
    gap: 16px;
  }
  
  .nav-item {
    flex-direction: column;
    padding: 8px;
    min-width: 60px;
  }
  
  .nav-item span {
    font-size: 12px;
    margin-top: 4px;
  }
  
  .nav-icon {
    margin-right: 0;
    margin-bottom: 4px;
  }
} 